




























import { Component, Vue } from "vue-property-decorator";
import Axios from "axios";

interface State {
  value: string;
  timestamp: string;
}

interface Response {
  current: State;
  history: State[];
}

@Component
export default class AnyoneThere extends Vue {
  state: Response = {
    current: { value: "UNKNOWN", timestamp: "" },
    history: [],
  };
  interval: number | undefined;
  update(): void {
    Axios.get("https://amiworking.shapingideas.fyi").then((r) => {
      this.state = r.data;
    });
  }

  created(): void {
    this.update();
    this.interval = setInterval(this.update, 500);
  }

  destroyed(): void {
    clearInterval(this.interval);
  }

  getStateClass(state: string): string {
    switch (state) {
      case "PRESENT":
        return "state-present";
      case "NOT_PRESENT":
        return "state-not-present";
      default:
        return "";
    }
  }

  prettyPrintState(state: string): string {
    switch (state) {
      case "PRESENT":
        return "üôâ";
      case "NOT_PRESENT":
        return "üôà";
      default:
        return "ü§ê";
    }
  }

  prettyPrintDate(d: string): string {
    return new Intl.DateTimeFormat("no", {
      year: "numeric",
      month: "numeric",
      day: "numeric",
      hour: "numeric",
      minute: "numeric",
      second: "numeric",
    }).format(Date.parse(d));
  }

  get history(): State[] {
    return this.state.history.reverse();
  }
}
